/*电商产品页*/


/*容器*/

.ECproduct {
	background-image: url(../img/EC.jpg);
}


/*头部*/

.cC_head {
	width: 100%;
	position: absolute;
	text-align: center;
	top: 10%;
}

.cC_head>h2 {
	font-size: 40px;
	line-height: 40px;
	color: #fff;
	font-weight: 700;
	font-family: Arial;
	text-transform: uppercase;
}

.cC_head>.cC_line {
	width: 40px;
	height: 2px;
	background-color: #fff;
	margin-left: auto;
	margin-right: auto;
	margin-top: 15px;
	margin-bottom: 15px;
}

.cC_head>.cC_txt {
	font-size: 20px;
	line-height: 20px;
	color: #fff;
}


/*头部结束*/


/*轮播内容*/

.EC_con {
	width: 1140px;
	height: 350px;
	position: absolute;
	left: 50%;
	top: 30%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}


/*左右按钮*/

.EC_con>span {
	display: block;
	z-index: 99;
	overflow: hidden;
	cursor: pointer;
	position: absolute;
	width: 52px;
	height: 52px;
	top: 150px;
}


/*上一个按钮*/

.EC_con>.prev {
	background: url(../img/ECbtns.png) left bottom no-repeat;
	left: -70px;
}


/*下一个按钮*/

.EC_con>.next {
	background: url(../img/ECbtns.png) right bottom no-repeat;
	right: -70px;
}


/*按钮悬浮时状态*/

.EC_con>span:hover {
	background-position-y: top;
}


/*列表内容容器*/

.EC_con ul {
	position: relative;
	width: 1140px;
	height: 350px;
}


/*列表项*/

.EC_con li {
	width: 240px;
	height: 350px;
	position: absolute;
	overflow: hidden;
	-webkit-transition: .5s;
	-o-transition: .5s;
	transition: .5s;
	cursor: pointer;
}


/*每一个项的类*/

.EC_con li.cld1 {
	-webkit-transform: scaleX(.88) scaleY(.9);
	    -ms-transform: scaleX(.88) scaleY(.9);
	        transform: scaleX(.88) scaleY(.9);
	left: -12px;
	top: 0%;
	z-index: 1;
}

.EC_con li.cld5 {
	-webkit-transform: scaleX(.88) scaleY(.9);
	    -ms-transform: scaleX(.88) scaleY(.9);
	        transform: scaleX(.88) scaleY(.9);
	left: 913px;
	top: 0px;
	z-index: 1;
}

.EC_con li.cld2 {
	-webkit-transform: scaleX(.93) scaleY(.95);
	    -ms-transform: scaleX(.93) scaleY(.95);
	        transform: scaleX(.93) scaleY(.95);
	left: 214px;
	top: 0px;
	z-index: 2;
}

.EC_con li.cld4 {
	-webkit-transform: scaleX(.93) scaleY(.95);
	    -ms-transform: scaleX(.93) scaleY(.95);
	        transform: scaleX(.93) scaleY(.95);
	left: 686px;
	top: 0px;
	z-index: 2;
}

.EC_con li.cld3 {
	-webkit-transform: translateX(-50%) scale(1);
	    -ms-transform: translateX(-50%) scale(1);
	        transform: translateX(-50%) scale(1);
	left: 50%;
	top: 0px;
	z-index: 2;
}


/*每一个项的类 END*/

.EC_con li a img {
	width: 100%;
	opacity: .5;
	height: 100%;
	-webkit-transition: .5s;
	-o-transition: .5s;
	transition: .5s;
}


/*介绍文本的内容*/

.EC_txt {
	position: absolute;
	left: 0;
	bottom: -147px;
	width: 100%;
	height: 210px;
	line-height: 40px;
	background: rgba(0, 0, 0, .5);
	text-indent: 8px;
	color: #fff;
	-webkit-transition: .5s;
	-o-transition: .5s;
	transition: .5s;
}

.EC_txt>h3 {
	font-family: "微软雅黑";
	font-size: 18px;
	margin-top: 10px;
	margin-left: 20px;
	font-weight: 500;
}

.EC_txt>p {
	font-family: "微软雅黑";
	line-height: 20px;
	padding: 15px 20px;
	font-size: 13px;
	color: #a1a1a1;
	height: 122px;
	overflow: hidden;
}


/*介绍文本的内容 End*/

.EC_con li.cld3 img {
	opacity: 1;
}

.EC_con li.cld3 .EC_txt {
	bottom: 0px;
}

.EC_con li:hover img {
	opacity: 1;
}


