/*招聘*/

.Recruit {
	width: 100%;
	height: 100%;
	background: #1a1a1a;
	position: relative;
}


/*顶部控制按钮*/

.controller {
	width: 100%;
	text-align: right;
	padding-top: 30px;
	padding-bottom: 20px;
	margin-left: 1%;
	font-size: 0px;
}

.controller a {
	display: inline-block;
	width: 50px;
	height: 50px;
	border: 1px solid #4c5058;
	text-align: center;
	font-size: 0px;
	position: relative;
	vertical-align: top;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}

.controller a:hover {
	background-color: #26ede6;
	border: 1px solid #26ede6;
}


/*返回按钮*/

.controller .ctl_back {
	padding: 10px;
	margin-right: 30px;
	position: relative;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.controller .ctl_back>span {
	display: inline-block;
	width: 8px;
	height: 8px;
	background: #4c5058;
	margin-right: 2px;
	margin-bottom: 2px;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}

.controller .ctl_back>span:nth-child(3n+3) {
	margin-right: 0px;
}


/*上一个/下一个按钮*/

.controller a.ctl_prev>span,
.controller a.ctl_next>span {
	width: 18px;
	height: 2px;
	background: #4c5058;
	position: absolute;
	left: 50%;
	top: 50%;
}

.controller a.ctl_prev>span:nth-child(1) {
	-webkit-transform-origin: left;
	    -ms-transform-origin: left;
	        transform-origin: left;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
}

.controller a.ctl_prev>span:nth-child(2) {
	-webkit-transform-origin: left;
	    -ms-transform-origin: left;
	        transform-origin: left;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
}

.controller a.ctl_next>span:nth-child(1) {
	-webkit-transform-origin: right;
	    -ms-transform-origin: right;
	        transform-origin: right;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
}

.controller a.ctl_next>span:nth-child(2) {
	-webkit-transform-origin: right;
	    -ms-transform-origin: right;
	        transform-origin: right;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
}

.controller a.ctl_back:hover span {
	background: #fff;
}


/*招聘列表项*/

.Recruit_items {
	width: 100%;
	height: 100%;
	overflow-y: auto;
	overflow-x: hidden;
	padding-left: 6%;
	padding-right: 6%;
	padding-bottom: 5%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.Recruit_list {
	width: 100%;
	overflow: hidden;
	margin-top: 50px;
}

.Recruit_list>li {
	border: 2px solid #424959;
	text-align: center;
	width: 22%;
	padding-top: 35px;
	padding-bottom: 35px;
	margin-right: 3%;
	margin-bottom: 3%;
	float: left;
	-webkit-transition: all .4s ease 0s;
	-o-transition: all .4s ease 0s;
	transition: all .4s ease 0s;
	cursor: pointer;
}

.Recruit_list>li:hover {
	border: 2px solid #26ede6;
}

.Recruit_list>li>h3 {
	font-size: 18px;
	color: #fff;
	-webkit-transition: all .4s ease 0s;
	-o-transition: all .4s ease 0s;
	transition: all .4s ease 0s;
	font-weight: 500;
}

.Recruit_list>li:hover h3 {
	color: #26ede6;
}

.Recruit_list>li>p {
	font-size: 14px;
	color: #fff;
}

.Recruit_list>li>div.line {
	width: 20px;
	height: 2px;
	margin: 24px auto;
	background-color: #fff;
}

.Recruit_list>li:nth-child(4n) {
	margin-right: 0px;
}

.Recruit_detail {
	padding-left: 6%;
	padding-right: 6%;
	width: 100%;
	height: 100%;
	overflow-y: auto;
	overflow-x: hidden;
	padding-bottom: 5%;
	background: #1a1a1a;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	opacity: 0;
	display: none;
	position: absolute;
	left: 0px;
	top: 0px;
}

.Recruit_detail_con {
	margin-top: 70px;
	width: 100%;
}

.Recruit_detail_con>div.Recruit_title {
	font-size: 22px;
	line-height: 64px;
	border: 1px solid #434343;
	border-right: 0;
	border-left: 0;
	color: #fff;
	padding-left: 10px;
}

.detail_box {
	padding: 54px 0 14px 54px;
	border-bottom: 1px solid #434343;
	margin-bottom: 30px;
	font-size: 14px;
	color: #fff;
	line-height: 30px;
}

.detail_btm {
	margin-bottom: 50px;
	font-size: 18px;
	color: #fff;
}

.detail_mail {
	color: #26ede6;
}


