/*新闻页*/

.news {
	width: 100%;
	height: 100%;
	background: #1a1a1a;
	overflow-y: auto;
	padding-left: 6%;
	padding-right: 6%;
	padding-bottom: 5%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: relative;
}


/*顶部控制按钮*/

.controller {
	width: 100%;
	text-align: right;
	padding-top: 30px;
	padding-bottom: 20px;
	margin-left: 1%;
	font-size: 0px;
}

.controller a {
	display: inline-block;
	width: 50px;
	height: 50px;
	border: 1px solid #4c5058;
	text-align: center;
	font-size: 0px;
	position: relative;
	vertical-align: top;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}

.controller a:hover {
	background-color: #26ede6;
	border: 1px solid #26ede6;
}


/*返回按钮*/

.controller .ctl_back {
	padding: 10px;
	margin-right: 30px;
	position: relative;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.controller .ctl_back>span {
	display: inline-block;
	width: 8px;
	height: 8px;
	background: #4c5058;
	margin-right: 2px;
	margin-bottom: 2px;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}

.controller .ctl_back>span:nth-child(3n+3) {
	margin-right: 0px;
}


/*上一个/下一个按钮*/

.controller a.ctl_prev>span,
.controller a.ctl_next>span {
	width: 18px;
	height: 2px;
	background: #4c5058;
	position: absolute;
	left: 50%;
	top: 50%;
}

.controller a.ctl_prev>span:nth-child(1) {
	-webkit-transform-origin: left;
	    -ms-transform-origin: left;
	        transform-origin: left;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
}

.controller a.ctl_prev>span:nth-child(2) {
	-webkit-transform-origin: left;
	    -ms-transform-origin: left;
	        transform-origin: left;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
}

.controller a.ctl_next>span:nth-child(1) {
	-webkit-transform-origin: right;
	    -ms-transform-origin: right;
	        transform-origin: right;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
}

.controller a.ctl_next>span:nth-child(2) {
	-webkit-transform-origin: right;
	    -ms-transform-origin: right;
	        transform-origin: right;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
}

.controller a.ctl_back:hover span {
	background: #fff;
}


/*新闻列表项*/

.news_items {
	width: 100%;
}


/*列表中的每一项*/

.news_item {
	padding: 12px 15px;
	border: 2px solid #1a1a1a;
	-webkit-transition: all .4s ease 0s;
	-o-transition: all .4s ease 0s;
	transition: all .4s ease 0s;
	margin-bottom: 10px;
	overflow: hidden;
	cursor: pointer;
}

.news_item:hover {
	border: 2px solid #26ede6;
}


/*栏目左侧图片*/

.img_box {
	width: 150px;
	height: 83px;
	margin-right: 70px;
	float: left;
	overflow: hidden;
	text-align: center;
	font-size: 0px;
}

.img_box>img {
	max-width: 150px;
	max-height: 83px;
}


/*栏目右侧文字盒子*/

.txt_box {
	width: calc(100% - 235px);
	float: left;
}

.txt_box>h3 {
	font-size: 18px;
	font-weight: 700;
	margin-bottom: 20px;
	color: #fff;
}

.news_item:hover .txt_box>h3 {
	color: #26ede6;
}

.txt_box>p {
	color: #5f5f5f;
	font-size: 14px;
}

.txt_box>p>span {
	margin-right: 15px;
}


/*新闻详情页*/

.news_detail {
	display: none;
	opacity: 0;
}

.news_detail {
	width: 100%;
	position: absolute;
	left: 0px;
	top: 0px;
	overflow: hidden;
	background: #1a1a1a;
	padding-left: 6%;
	padding-right: 6%;
	padding-bottom: 5%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}


