/*子公司页*/


/*容器*/

.childCompany {
	background-image: url(../img/childCompany.jpg);
}


/*头部*/

.cC_head {
	width: 100%;
	position: absolute;
	text-align: center;
	top: 10%;
}

.cC_head>h2 {
	font-size: 40px;
	line-height: 40px;
	color: #fff;
	font-weight: 700;
	font-family: Arial;
	text-transform: uppercase;
}

.cC_head>.cC_line {
	width: 40px;
	height: 2px;
	background-color: #fff;
	margin-left: auto;
	margin-right: auto;
	margin-top: 15px;
	margin-bottom: 15px;
}

.cC_head>.cC_txt {
	font-size: 20px;
	line-height: 20px;
	color: #fff;
}


/*头部结束*/


/*轮播展示*/

.cC_slider {
	width: 1080px;
	position: absolute;
	top: 170px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}


/*按钮*/

.cC_btn {
	font-size: 0;
	line-height: 0;
	position: absolute;
	top: 45%;
	width: 25px;
	height: 25px;
	margin-top: -10px;
	cursor: pointer;
	background: #fff;
	opacity: .75;
	font-family: slick;
	font-size: 20px;
	line-height: 25px;
	text-align: center;
	color: #000;
	border-radius: 50%;
	font-weight: bold;
}


/*左按钮*/

.cC_btn_left {
	left: -25px;
}


/*右按钮*/

.cC_btn_right {
	right: -25px;
}

.cC_btn:hover {
	opacity: 1;
}

.cC_slider>.cC_btn_out {
	opacity: .25;
}


/*滑动内容*/

.cC_con {
	width: 100%;
	height: 355px;
	overflow: hidden;
}


/*宽盒子*/

.cC_con_long {
	width: calc(270px*6);
	height: 100%;
	overflow: hidden;
	-webkit-transition: .5s;
	-o-transition: .5s;
	transition: .5s;
}

.cC_con_long>div {
	width: 270px;
	height: 100%;
	float: left;
	position: relative;
}

.cC_con_main {
	width: 220px;
	height: 290px;
	background-color: #fff;
	position: absolute;
	overflow: hidden;
	left: 30px;
	top: 15px;
	-webkit-transition: all .4s ease-in-out;
	-o-transition: all .4s ease-in-out;
	transition: all .4s ease-in-out;
}

.cC_con_main>img {
	background-color: #fff;
	width: 170px;
	position: absolute;
	top: 50%;
	left: 50%;
	max-height: 290px;
	-webkit-transform: translateX(-50%) translateY(-50%);
	    -ms-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
}

.cC_con_hover_main {
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #0ff;
	-webkit-transition: all .4s ease;
	-o-transition: all .4s ease;
	transition: all .4s ease;
}

.cC_con_hover_main>img {
	width: 170px;
	position: absolute;
	left: 35px;
	top: 50px;
	opacity: 0;
	max-height: 290px;
	-webkit-transition: all .3s ease-in-out .2s;
	-o-transition: all .3s ease-in-out .2s;
	transition: all .3s ease-in-out .2s
}

.cC_con_hover_main>p {
	position: absolute;
	bottom: 0;
	text-align: left;
	padding: 0 24px;
	font-size: 13px;
	color: #134b7f;
	-webkit-transition: all .3s ease .2s;
	-o-transition: all .3s ease .2s;
	transition: all .3s ease .2s;
}

.cC_con_detail {
	width: 115px;
	height: 42px;
	background-color: #0ff;
	display: block;
	margin-top: 2px;
	font-size: 12px;
	color: #134b7f;
	line-height: 38px;
	opacity: 0;
	position: relative;
	left: 30px;
	top: 300px;
	-webkit-transition: all .4s ease-in-out;
	-o-transition: all .4s ease-in-out;
	transition: all .4s ease-in-out;
	text-align: center;
	-webkit-transform: rotateX(-80deg);
	        transform: rotateX(-80deg);
	-webkit-transform-origin: top;
	    -ms-transform-origin: top;
	        transform-origin: top;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}

.cC_con_long>div:hover .cC_con_main {
	width: 240px;
	left: 20px;
	top: -10px;
	height: 320px;
}

.cC_con_long>div:hover .cC_con_hover_main {
	top: 0;
}

.cC_con_long>div:hover .cC_con_hover_main>img {
	opacity: 1;
	top: 30px;
}

.cC_con_long>div:hover .cC_con_hover_main>p {
	bottom: 20px;
}

.cC_con_long>div:hover .cC_con_detail {
	opacity: 1;
	top: 310px;
	left: 20px;
	-webkit-transform: rotateX(0deg);
	        transform: rotateX(0deg);
}


