/*
 * 尾部样式
 * */

.footer {
	position: fixed;
	left: 30px;
	bottom: 28px;
	color: #6d7680;
	z-index: 9;
	font-family: Arial;
	font-weight: 900;
	width: calc(100% - 230px);
}

.txt1 {
	font-size: 16px;
	margin-bottom: 6px;
}

.txt2 {
	font-size: 12px;
	font-weight: 400;
	text-transform: uppercase;
}

.txt2>a {
	font-size: 12px;
	font-weight: 400;
	color: #6d7680;
}

.footer_hide {
	display: none;
}


