/*
 * 右侧导航样式
 * */


/*容器开始*/

.sideNav {
	width: 150px;
	height: 100%;
	position: absolute;
	right: 0px;
	top: 0px;
	-webkit-transition: .4s linear;
	-o-transition: .4s linear;
	transition: .4s linear;
}

.sideNavIn {
	right: -150px;
}


/*容器结束*/


/*展开缩回按钮开始*/

.sideNav_btn {
	width: 25px;
	height: 25px;
	position: absolute;
	left: -80px;
	top: 30px;
	cursor: pointer;
	-webkit-transform: translateY(0px) rotateZ(0deg);
	    -ms-transform: translateY(0px) rotate(0deg);
	        transform: translateY(0px) rotateZ(0deg);
}

.sideNav_btn>div {
	width: 25px;
	height: 3px;
	background: #FFFFFF;
	margin-bottom: 5px;
	border-radius: 1.5px;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}


/*切换展开缩回按钮类名*/

.sideNav_btn_In>div:last-child {
	display: none;
}

.sideNav_btn_In>div:first-child {
	margin-top: 8px;
	-webkit-transform: translateY(8px) rotateZ(45deg);
	    -ms-transform: translateY(8px) rotate(45deg);
	        transform: translateY(8px) rotateZ(45deg);
}

.sideNav_btn_In>div {
	-webkit-transform: rotateZ(-45deg);
	    -ms-transform: rotate(-45deg);
	        transform: rotateZ(-45deg);
}


/*展开缩回按钮结束*/


/*导航主体内容开始*/

.sideNav_con {
	width: 150px;
	height: 100%;
	background: #fff;
	position: absolute;
	top: 0px;
	right: 0px;
}


/*logo*/

.logo {
	width: 65px;
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-top: 10px;
}


/*logo结束*/


/*导航*/

.s_c_list {
	width: 100%;
	position: absolute;
	left: 0px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.s_c_list>li {
	width: 100%;
	height: 40px;
	line-height: 20px;
	text-align: center;
	overflow: hidden;
	margin-bottom: 10px;
	font-size: 16px;
}

.s_c_list a {
	display: block;
	color: #666;
	-webkit-transition: all .4s ease 0s;
	-o-transition: all .4s ease 0s;
	transition: all .4s ease 0s;
	margin-bottom: 20px;
	margin-top: 0;
}

.s_c_list .in {
	color: #1d72bc;
}

.s_c_list>li:hover>.ot {
	margin-top: -40px;
}

.s_c_list>.active>.ot {
	margin-top: -40px;
}


/*导航结束*/


/*底部联系方式  二维码*/

.s_c_btm {
	width: 120px;
	position: absolute;
	left: 50%;
	bottom: 20px;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.s_c_btm ul {
	margin-top: 12px;
	font-size: 0px;
}

.s_c_btm li {
	width: 40px;
	height: 30px;
	display: inline-block;
}


/*电话号*/

.s_c_btm>p {
	width: auto;
	font-size: 16px;
	color: #232323;
	padding-bottom: 10px;
	border-bottom: 1px solid #ebebeb;
}


/*小图标*/

.s_c_btm span {
	display: inline-block;
	width: 40px;
	height: 32px;
	-webkit-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out;
	cursor: pointer;
}

.weiboQR {
	background: url(../img/QRcodeDft.png) no-repeat 5px 0px;
}

.zhitangQR {
	background: url(../img/QRcodeDft.png) no-repeat -35px 0px;
}

.qizhiQR {
	background: url(../img/QRcodeDft.png) no-repeat -75px 0px;
}


/*鼠标悬浮改变样式*/

.s_c_btm span:hover {
	background-image: url(../img/QRcodeIn.png);
}

.s_c_btm span:hover+div {
	display: block;
}


/*大的二维码*/

.weiboQRBig,
.zhitangBig,
.qizhiQRBig {
	position: absolute;
	bottom: 43px;
	left: 0px;
	font-size: 0px;
	left: ;
	display: none;
}

.s_c_btm img {
	border: 3px solid #dce0e4;
}


/*二维码小三角*/

.weiboQRBig:after,
.zhitangBig:after,
.qizhiQRBig:after {
	content: 'fasfsads';
	width: 0px;
	height: 0px;
	border-left: 10px solid transparent;
	border-right: 10px solid transparent;
	border-top: 9px solid #dce0e4;
	position: absolute;
	bottom: -9px;
}

.weiboQRBig:after {
	left: 8px;
}

.zhitangBig:after {
	left: 50px;
}

.qizhiQRBig:after {
	left: 90px;
}


/*底部联系方式  二维码 结束*/


