/*营销服务*/


/*内容主体容器*/

.marketing_con {
	width: 1452px;
	height: 100%;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}


/*列表项*/

.marketing_con>li {
	width: 360px;
	height: 100%;
	margin-right: 3px;
	float: left;
	background: yellowgreen;
	position: relative;
	cursor: pointer;
}


/*列表背景*/

.marketing_con>li>img {
	width: 100%;
	position: absolute;
	left: 0px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
	-webkit-transition: all .4s ease 0s;
	-o-transition: all .4s ease 0s;
	transition: all .4s ease 0s;
}


/*列表文字*/

.mc_txtWrap {
	width: 100%;
	height: 35%;
	position: absolute;
	bottom: 0px;
	background: #131314;
	-webkit-transition: all .4s ease 0s;
	-o-transition: all .4s ease 0s;
	transition: all .4s ease 0s;
}

.mc_txtWrap_tit {
	font-family: Arial;
	font-size: 20px;
	color: #ccc;
	text-align: center;
	margin-top: 10%;
	text-transform: uppercase;
	-webkit-transition: all .4s ease 0s;
	-o-transition: all .4s ease 0s;
	transition: all .4s ease 0s;
}

.mc_txtWrap_con {
	width: 100%;
}

.mc_txtWrap_con>p:nth-child(1) {
	font-size: 22px;
	color: #ccc;
	text-align: center;
	margin-bottom: 5px;
	width: 100%;
	position: absolute;
	bottom: 25%;
	-webkit-transition: all .4s ease 0s;
	-o-transition: all .4s ease 0s;
	transition: all .4s ease 0s;
}

.mc_txtWrap_con>p:nth-child(2) {
	font-size: 14px;
	color: #666;
	text-align: center;
	width: 100%;
	position: absolute;
	bottom: 15%;
	-webkit-transition: all .4s ease .1s;
	-o-transition: all .4s ease .1s;
	transition: all .4s ease .1s;
}

.mc_txtWrap_con>p:nth-child(3) {
	font-size: 13px;
	color: #ccc;
	display: block;
	width: 80%;
	height: 56px;
	overflow: hidden;
	position: absolute;
	left: 10%;
	bottom: -100%;
	text-align: center;
	-webkit-transition: all .4s ease .2s;
	-o-transition: all .4s ease .2s;
	transition: all .4s ease .2s;
}

.marketing_con>li:hover>img {
	top: 45%;
}

.marketing_con>li:hover>.mc_txtWrap {
	height: 40%;
	background-color: #26ede6;
}

.marketing_con>li:hover .mc_txtWrap_tit {
	color: #fff;
}

.marketing_con>li:hover .mc_txtWrap_con>p:nth-child(1) {
	bottom: 45%;
	color: #fff;
}

.marketing_con>li:hover .mc_txtWrap_con>p:nth-child(2) {
	bottom: 40%;
	color: #fff;
}

.marketing_con>li:hover .mc_txtWrap_con>p:nth-child(3) {
	bottom: 12%;
	color: #fff;
}


/*下侧拖动条*/

.scrollMove {
	width: 100%;
	height: 16px;
	position: absolute;
	bottom: 4px;
	left: 0;
	overflow: hidden;
	cursor: pointer;
}


/*左按钮*/

.scrollMove_left {
	background-image: url(../img/tubiao.png);
	background-repeat: no-repeat;
	opacity: .4;
	display: block;
	position: relative;
	width: 20px;
	height: 16px;
	overflow: hidden;
	margin: 0 auto;
	cursor: pointer;
	float: left;
	background-position: -80px -40px;
}


/*右按钮*/

.scrollMove_right {
	background-image: url(../img/tubiao.png);
	background-repeat: no-repeat;
	opacity: .4;
	display: block;
	position: relative;
	width: 20px;
	height: 16px;
	overflow: hidden;
	margin: 0 auto;
	cursor: pointer;
	float: right;
	margin-left: -40px;
	background-position: -80px -56px;
}

.scrollMove_child {
	width: 1326px;
	height: 8px;
	position: absolute;
	left: 20px;
	top: 4px;
	overflow: hidden;
}

.scrollMove_child>div:nth-child(1) {
	width: 100%;
	height: 2px;
	background: rgba(0, 0, 0, .15);
	margin-top: 3px;
}

.scrollMove_child>div:nth-child(2) {
	width: 1247px;
	height: 8px;
	background: rgba(0, 0, 0, .75);
	position: absolute;
	top: 0px;
	border-radius: 4px;
}


