/*详情页类型一*/


/*外层*/

.DetailType1 {
	width: 100%;
	height: 100%;
	overflow: hidden;
	position: relative;
}


/*外层列表项容器*/

.DT1_items {
	width: 100%;
	height: 100%;
	overflow: auto;
}


/*外层列表项*/

.DT1_item {
	width: 33%;
	height: 90%;
	overflow: hidden;
	float: left;
	cursor: pointer;
	position: relative;
}

.DT1_item>img {
	width: 100%;
}


/*外层列表项蒙版*/

.DT1_item_mesk {
	width: 100%;
	height: 100%;
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, .8);
	z-index: 2;
	color: #fff;
	text-align: center;
	left: 0;
	top: 0;
	overflow: hidden;
	opacity: 0;
	-webkit-transition: all .6s ease-in-out 0s;
	-o-transition: all .6s ease-in-out 0s;
	transition: all .6s ease-in-out 0s;
}


/*外层列表项蒙版上的字*/

.DT1_item_mesk>.DT1_txt {
	position: relative;
	top: 50%;
	width: 80%;
	margin: 0 auto;
	-webkit-transform: translateY(-30%);
	    -ms-transform: translateY(-30%);
	        transform: translateY(-30%);
	-webkit-transition: all .6s ease 0s;
	-o-transition: all .6s ease 0s;
	transition: all .6s ease 0s;
}

.DT1_item_mesk>.DT1_txt>h3 {
	font-size: 26px;
	margin: 26px 0;
	font-weight: bold;
}

.DT1_item_mesk>.DT1_txt>p {
	font-size: 16px;
	margin: 16px 0;
}

.DT1_item_mesk>.DT1_txt>p:nth-of-type(2) {
	font-size: 14px;
	margin: 14px 0;
}

.DT1_item_mesk>.DT1_txt>.line {
	width: 24px;
	height: 3px;
	background-color: #fff;
	margin: 20px auto;
}

.DT1_item:hover .DT1_item_mesk {
	opacity: 1;
}

.DT1_item:hover .DT1_item_mesk>.DT1_txt {
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
}


/*外层列表项蒙版上的字end*/


/*查看进一步的详请*/

.DT1_itemShow {
	width: 100%;
	height: 100%;
	padding: 0px 6% 5% 6%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	overflow: auto;
	background: #1a1a1a;
	position: absolute;
	left: 0px;
	top: 0px;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
	opacity: 0;
	display: none;
	z-index: 3;
}


/*顶部控制按钮*/

.controller {
	width: 100%;
	text-align: right;
	padding-top: 30px;
	padding-bottom: 20px;
	margin-left: 1%;
	font-size: 0px;
}

.controller a {
	display: inline-block;
	width: 50px;
	height: 50px;
	border: 1px solid #4c5058;
	text-align: center;
	font-size: 0px;
	position: relative;
	vertical-align: top;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}

.controller a:hover {
	background-color: #26ede6;
	border: 1px solid #26ede6;
}


/*返回按钮*/

.ctl_back {
	padding: 10px;
	margin-right: 30px;
	position: relative;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.ctl_back>span {
	display: inline-block;
	width: 8px;
	height: 8px;
	background: #4c5058;
	margin-right: 2px;
	margin-bottom: 2px;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}

.ctl_back>span:nth-child(3n+3) {
	margin-right: 0px;
}


/*上一个/下一个按钮*/

.controller a:nth-child(2)>span,
.controller a:nth-child(3)>span {
	width: 18px;
	height: 2px;
	background: #4c5058;
	position: absolute;
	left: 50%;
	top: 50%;
}

.controller a:nth-child(2)>span:nth-child(1) {
	-webkit-transform-origin: left;
	    -ms-transform-origin: left;
	        transform-origin: left;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
}

.controller a:nth-child(2)>span:nth-child(2) {
	-webkit-transform-origin: left;
	    -ms-transform-origin: left;
	        transform-origin: left;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
}

.controller a:nth-child(3)>span:nth-child(1) {
	-webkit-transform-origin: right;
	    -ms-transform-origin: right;
	        transform-origin: right;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
}

.controller a:nth-child(3)>span:nth-child(2) {
	-webkit-transform-origin: right;
	    -ms-transform-origin: right;
	        transform-origin: right;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
}

.controller a.ctl_back:hover span {
	background: #fff;
}


/*外层列表项蒙版上的字end*/


