@charset "UTF-8";

/* css reset*/

html {
	height: 100%;
	width: 100%;
	color: #000;
	background: #FFF;
	font-family: "微软雅黑", arial, sans-serif;
}

body {
	width: 100%;
	min-width: 900px;
	height: 100%;
	background-color: #000;
	overflow: hidden;
}

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td,
strong {
	padding: 0;
	margin: 0;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

fieldset,
img {
	border: 0;
}

a {
	text-decoration: none;
	outline: none;
}

var,
em,
i {
	font-style: normal;
}

address,
caption,
cite,
code,
dfn,
em,
th,
var,
optgroup {
	font-style: inherit;
	font-weight: inherit;
}

del,
ins {
	text-decoration: none;
}

caption,
th {
	text-align: left;
}

li {
	list-style: none;
}


q:before,
q:after {
	content: '';
}

abbr,
acronym {
	border: 0;
	font-variant: normal;
}

sup {
	vertical-align: baseline;
}

sub {
	vertical-align: baseline;
}

legend {
	color: #000;
}

input,
button,
textarea,
select,
optgroup,
option {
	font-family: inherit;
	font-size: inherit;
	font-style: inherit;
	font-weight: inherit;
}

input,
button,
textarea,
select {
	*font-size: 100%;
}

.clear {
	clear: both;
	height: 0px;
	overflow: hidden;
}

.clearfix:after {
	clear: both;
	display: block;
	content: "...";
	visibility: hidden;
	height: 0;
	font-size: 0;
}

input[type="submit"],
input[type="reset"],
input[type="button"],
button {
	-webkit-appearance: none;
	outline: none;
	cursor: pointer;
}

.clearfix {
	*zoom: 1;
}


