/*练习我们*/


/*容器*/

.map {
	background: #1f2428;
}


/*地图*/

.mapIn {
	width: 509px;
	height: 418px;
	background: url(../img/map.png) no-repeat center;
	position: absolute;
	left: 50%;
	top: 6.5%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}

/*城市*/

.mapIn>span {
	position: absolute;
	font-size: 13px;
	color: #fff;
	display: block;
	text-align: center;
	font-weight: bold;
}

.mapIn>span b {
	position: absolute;
	width: 80px;
	top: -20px;
	left: -25px;
}

/*圆点*/

.mapIn>span:before {
	background: #b1b3b6;
	content: '';
	display: inline-block;
	margin: 0 auto;
	width: 10px;
	height: 10px;
	border-radius: 30px;
	margin-right: 10px;
	vertical-align: middle;
	-webkit-transition: all .3s;
	-o-transition: all .3s;
	transition: all .3s;
}

.mapIn>span.in:before {
	background: #00FFFE;
	width: 16px;
	height: 16px;
	margin-top: -3px;
}

.mapIn>span:hover:before {
	background: #00FFFE;
	width: 16px;
	height: 16px;
	margin-top: -3px;
}

.mapIn>span:nth-child(1) {
	left: 375px;
	top: 145px;
}

.mapIn>span:nth-child(2) {
	left: 385px;
	top: 155px;
}

.mapIn>span:nth-child(3) {
	left: 426px;
	top: 269px;
}

.mapIn>span:nth-child(4) {
	left: 414px;
	top: 312px;
}

.mapIn>span:nth-child(5) {
	left: 415px;
	top: 185px;
}

.mapIn>span:nth-child(6) {
	left: 303px;
	top: 272px;
}

/*下面的文字*/

.txt {
	width: 100%;
	text-align: center;
	position: absolute;
	top: 74%;
	height: 78px;
}

.txt>div {
	z-index: 0;
	opacity: 0;
	width: 100%;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
	font-size: 14px;
	color: #fff;
	position: absolute;
	top: 0px;
}

.txt>div>p:nth-child(1) {
	font-size: 22px;
	margin-bottom: 10px;
	font-weight: 500;
}

.txt>div.in {
	display: block;
	z-index: 2;
	opacity: 1;
	-webkit-transition: .3s .4s;
	-o-transition: .3s .4s;
	transition: .3s .4s;
}


