/*
 * home页*/


/*容器*/

.home {
	background-image: url(../img/homeBj.jpg);
}


/*旋转背景*/

.circleRotate {
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0px;
	top: 0px;
	overflow: hidden;
}

.circleRotate>div {
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0px;
	top: 0px;
	-webkit-transform: rotate(0deg);
	    -ms-transform: rotate(0deg);
	        transform: rotate(0deg);
	background-size: cover;
}

.rotate1 {
	background: url(../img/1.png) center no-repeat;
	-webkit-animation: RotateA 3.5s cubic-bezier(.55, 1.2, .68, 1.2) alternate infinite;
	        animation: RotateA 3.5s cubic-bezier(.55, 1.2, .68, 1.2) alternate infinite;
}

.rotate2 {
	background: url(../img/2.png) center no-repeat;
	-webkit-animation: RotateB 5.5s cubic-bezier(.55, 1.2, .68, 1.2) alternate infinite;
	        animation: RotateB 5.5s cubic-bezier(.55, 1.2, .68, 1.2) alternate infinite;
}

.rotate3 {
	background: url(../img/3.png) center no-repeat;
	-webkit-animation: RotateA 3.5s cubic-bezier(.55, 1.2, .68, 1.2) alternate infinite;
	        animation: RotateA 3.5s cubic-bezier(.55, 1.2, .68, 1.2) alternate infinite;
}

.rotate4 {
	background: url(../img/4.png) center no-repeat;
	-webkit-animation: RotateB 5.5s cubic-bezier(.55, 1.2, .68, 1.2) alternate infinite;
	        animation: RotateB 5.5s cubic-bezier(.55, 1.2, .68, 1.2) alternate infinite;
}


/*旋转需要的关键帧*/

@-webkit-keyframes RotateA {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0)
	}
	30% {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg)
	}
	80% {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg)
	}
	100% {
		-webkit-transform: rotate(-20deg);
		        transform: rotate(-20deg)
	}
}

@keyframes RotateA {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0)
	}
	30% {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg)
	}
	80% {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg)
	}
	100% {
		-webkit-transform: rotate(-20deg);
		        transform: rotate(-20deg)
	}
}

@-webkit-keyframes RotateB {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0)
	}
	30% {
		-webkit-transform: rotate(-25deg);
		        transform: rotate(-25deg)
	}
	80% {
		-webkit-transform: rotate(-25deg);
		        transform: rotate(-25deg)
	}
	100% {
		-webkit-transform: rotate(75deg);
		        transform: rotate(75deg)
	}
}

@keyframes RotateB {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0)
	}
	30% {
		-webkit-transform: rotate(-25deg);
		        transform: rotate(-25deg)
	}
	80% {
		-webkit-transform: rotate(-25deg);
		        transform: rotate(-25deg)
	}
	100% {
		-webkit-transform: rotate(75deg);
		        transform: rotate(75deg)
	}
}


/*旋转背景结束*/


/*会动的小点*/

.debris {
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0px;
	top: 0px;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	-webkit-perspective: 800px;
	        perspective: 800px;
	-webkit-transform: rotateX(-45deg) rotateY(-45deg) rotateZ(0deg);
	        transform: rotateX(-45deg) rotateY(-45deg) rotateZ(0deg);
	-webkit-animation: Rotate 10s linear infinite;
	        animation: Rotate 10s linear infinite;
}

.debris>div {
	position: absolute;
	left: 50%;
	top: 50%;
	background: #fff;
	border-radius: 50%;
}


/*动的小点关键帧*/

@-webkit-keyframes Rotate {
	0% {
		-webkit-transform: rotateX(-15deg) rotateY(-15deg) rotateZ(0deg);
		        transform: rotateX(-15deg) rotateY(-15deg) rotateZ(0deg);
	}
	50% {
		-webkit-transform: rotateX(15deg) rotateY(15deg) rotateZ(180deg);
		        transform: rotateX(15deg) rotateY(15deg) rotateZ(180deg);
	}
	to {
		-webkit-transform: rotateX(-15deg) rotateY(-15deg) rotateZ(360deg);
		        transform: rotateX(-15deg) rotateY(-15deg) rotateZ(360deg)
	}
}

@keyframes Rotate {
	0% {
		-webkit-transform: rotateX(-15deg) rotateY(-15deg) rotateZ(0deg);
		        transform: rotateX(-15deg) rotateY(-15deg) rotateZ(0deg);
	}
	50% {
		-webkit-transform: rotateX(15deg) rotateY(15deg) rotateZ(180deg);
		        transform: rotateX(15deg) rotateY(15deg) rotateZ(180deg);
	}
	to {
		-webkit-transform: rotateX(-15deg) rotateY(-15deg) rotateZ(360deg);
		        transform: rotateX(-15deg) rotateY(-15deg) rotateZ(360deg)
	}
}


/*抖动效果*/

.shake {
	width: 530px;
	height: 223px;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	    -ms-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
}


/*默认状态*/

.shake_fir,
.shake_two,
.shake_two>img {
	position: absolute;
	left: 0px;
	top: 0px;
}

.shake_two {
	opacity: 0;
	-webkit-animation: Flickerlong 8s linear infinite forwards;
	        animation: Flickerlong 8s linear infinite forwards;
}

.shake_fir {
	-webkit-animation: outFlicker 8s linear infinite forwards;
	        animation: outFlicker 8s linear infinite forwards;
}


/*默认状态关键帧*/

@-webkit-keyframes outFlicker {
	0% {
		opacity: 0
	}
	1.5% {
		opacity: 1
	}
	2.7% {
		opacity: 0
	}
	3% {
		opacity: 1
	}
	7% {
		opacity: 0
	}
	9% {
		opacity: 1
	}
	100% {
		opacity: 1
	}
}

@keyframes outFlicker {
	0% {
		opacity: 0
	}
	1.5% {
		opacity: 1
	}
	2.7% {
		opacity: 0
	}
	3% {
		opacity: 1
	}
	7% {
		opacity: 0
	}
	9% {
		opacity: 1
	}
	100% {
		opacity: 1
	}
}

@-webkit-keyframes Flickerlong {
	0% {
		opacity: .5
	}
	1.5% {
		opacity: 0
	}
	2.7% {
		opacity: 1
	}
	3% {
