/*鼠标移入后状态*/

.center-mouse {
	width: 800px;
	height: 500px;
	position: absolute;
	left: 50%;
	top: 50%;
	margin-left: -400px;
	margin-top: -250px;
	color: #fff;
	border-radius: 50%;
}

