	display: block;
	left: 0;
	top: 0;
	position: absolute;
	width: 148px;
	background-repeat: no-repeat;
	opacity: 0;
}

.ct_center {
	text-align: center;
	position: relative;
	top: 50%;
	margin-top: -60px;
}

.ct_center>span {
	background-image: url(../img/txt-shake_big.png);
	background-repeat: no-repeat;
	display: block;
	position: absolute;
	top: -5px;
	left: 0;
	width: 410px;
	height: 100px;
	margin: 0 auto;
	opacity: 0;
}

.ct_center_img {
