/*成长历程*/

.hty {
	width: 100%;
	height: 100%;
	overflow: auto;
	padding-left: 6%;
	padding-right: 6%;
	padding-bottom: 5%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding-top: 60px;
	background: #1a1a1a;
}


/*年份列表*/

.hty_list {
	width: 100%;
	padding-bottom: 40px;
	border-bottom: 1px solid #434343;
	margin-bottom: 45px;
	overflow: hidden;
}


/*年*/

.year {
	width: 10%;
	float: left;
	font-size: 26px;
	font-family: arial;
	font-weight: 700;
	color: #fff;
	line-height: 28px;
}

.year_list {
	width: 90%;
	float: left;
}


/*月份列表*/

.year_item {
	margin-bottom: 25px;
	overflow: hidden;
}

.month {
	width: 10%;
	float: left;
	font-size: 18px;
	color: #8d8d8d;
	line-height: 28px;
}

.title {
	width: 13%;
	margin-right: 8%;
	font-size: 18px;
	color: #fff;
	float: left;
	position: relative;
	top: 4px;
}

.Y_txt {
	width: 67%;
	color: #8d8d8d;
	font-size: 13px;
	float: left;
	line-height: 18px;
}


