/*详情页*/

.paging {
	width: 80%;
	min-width: 700px;
	height: 100%;
	position: fixed;
	right: -80%;
	top: 0;
	z-index: 11;
	overflow: hidden;
	-webkit-transition: right .6s ease-in-out 0s;
	-o-transition: right .6s ease-in-out 0s;
	transition: right .6s ease-in-out 0s;
	background-color: #000;
}


/*关闭分页按钮*/

.closePaging {
	width: 150px;
	position: fixed;
	right: -256px;
	top: 20px;
	border: 2px solid #fff;
	display: block;
	padding: 6px 10px;
	color: #fff;
	z-index: 99;
	-webkit-transition: all .6s ease-in-out 0s;
	-o-transition: all .6s ease-in-out 0s;
	transition: all .6s ease-in-out 0s;
	cursor: pointer;
}

.closePaging>span {
	font-size: 22px;
	line-height: 34px;
	position: relative;
	color: #fff;
}

.closePaging_x {
	width: 28px;
	background: red;
	position: absolute;
	right: 15px;
	top: 50%;
	-webkit-transform: translateY(-50%) rotateZ(0deg);
	    -ms-transform: translateY(-50%) rotate(0deg);
	        transform: translateY(-50%) rotateZ(0deg);
	;
	-webkit-transition: all .6s ease-in-out 0s;
	;
	-o-transition: all .6s ease-in-out 0s;
	;
	transition: all .6s ease-in-out 0s;
}

.closePaging_x>span {
	width: 100%;
	height: 5px;
	background: #fff;
	-webkit-transform: rotateZ(45deg);
	    -ms-transform: rotate(45deg);
	        transform: rotateZ(45deg);
	position: absolute;
	top: 50%;
	margin-top: -2px;
	-webkit-transition: all .6s ease-in-out 0s;
	-o-transition: all .6s ease-in-out 0s;
	transition: all .6s ease-in-out 0s;
}

.closePaging_x>span:nth-child(1) {
	-webkit-transform: rotateZ(-45deg);
	    -ms-transform: rotate(-45deg);
	        transform: rotateZ(-45deg);
}

.closePaging:hover .closePaging_x {
	-webkit-transform: translateY(-50%) rotateZ(179deg);
	    -ms-transform: translateY(-50%) rotate(179deg);
	        transform: translateY(-50%) rotateZ(179deg);
	;
}

.closePaging:hover .closePaging_x>span {
	background: #26ede6;
}


/*关闭分页按钮结束*/


