/*关于我们*/


/*容器*/

.about {
	background-image: url(../img/about.jpg);
}


/*内容*/

.about_con {
	width: 90%;
	position: absolute;
	left: 50%;
	top: 28%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}


/*大标题*/

.a_c_tit {
	width: 100%;
	height: 80px;
	text-align: right;
	color: #242727;
	line-height: 30px;
}

.a_c_tit>p:nth-child(1) {
	font-size: 26px;
}

.a_c_tit>p:nth-child(2) {
	font-size: 22px;
}


/*主体内容*/

.a_c_list {
	width: 100%;
	height: 210px;
	background: black;
}


/*列表项*/

.list_cld {
	width: 25%;
	height: 100%;
	background: #191618;
	float: left;
	position: relative;
	cursor: pointer;
}


/*如果是偶数背景颜色不一样*/

.list_cld:nth-child(even) {
	background: #242727;
}


/*默认的内容*/

.list_cld_dft {
	position: absolute;
	left: 20px;
	bottom: 15px;
	color: #fff;
	-webkit-transition: .2s;
	-o-transition: .2s;
	transition: .2s;
}

.list_cld_dft>img {
	display: block;
}

.list_cld_dft>p:nth-of-type(1) {
	font-size: 14px;
	text-transform: uppercase;
}

.list_cld_dft>p:nth-of-type(2) {
	font-size: 18px;
}


/*默认的内容 end*/


/*鼠标悬浮是默认内容消失*/

.list_cld:hover .list_cld_dft {
	opacity: 0;
}


/*鼠标悬浮时的内容*/

.list_cld_hover {
	width: 90%;
	height: 90%;
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	    -ms-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	opacity: 0;
	-webkit-transition: .4s;
	-o-transition: .4s;
	transition: .4s;
}


/*会动的线*/

.hover_lines {
	width: 100%;
	height: 100%;
	position: relative;
}

.hover_lines>div:nth-child(1) {
	width: 100%;
	height: 2px;
	background: #26ede6;
	position: absolute;
	bottom: 0px;
	left: 0px;
}

.hover_lines>div:nth-child(2) {
	width: 2px;
	height: 0px;
	background: #26ede6;
	position: absolute;
	bottom: 0px;
	right: 0px;
	-webkit-transition: .2s linear;
	-o-transition: .2s linear;
	transition: .2s linear;
}

.hover_lines>div:nth-child(3) {
	width: 0%;
	height: 2px;
	background: #26ede6;
	position: absolute;
	top: 0px;
	right: 0px;
	-webkit-transition: .2s .2s ease-out;
	-o-transition: .2s .2s ease-out;
	transition: .2s .2s ease-out;
}

.hover_lines>div:nth-child(4) {
	width: 2px;
	height: 0%;
	background: #26ede6;
	position: absolute;
	bottom: 0px;
	left: 0px;
	-webkit-transition: .4s ease-in-out;
	-o-transition: .4s ease-in-out;
	transition: .4s ease-in-out;
}


/*会动的线 end*/


/*悬浮时的文本内容*/

.hover_con {
	width: 100%;
	height: 50px;
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
	overflow: hidden;
}


/*斜线*/

.bias {
	width: 50px;
	height: 2px;
	background: #26ede6;
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
	    -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
	        transform: translateX(-50%) translateY(-50%) rotateZ(45deg);
}


/*悬浮文本*/

.hover_txt {
	color: #0ff;
	font-size: 16px;
	-webkit-transition: all .4s ease-in-out .2s;
	-o-transition: all .4s ease-in-out .2s;
	transition: all .4s ease-in-out .2s;
}

.hover_txt:nth-child(2) {
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	    -ms-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	margin-left: -10px;
	margin-top: -34px;
}

.hover_txt:nth-child(3) {
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	    -ms-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	margin-left: 10px;
	margin-top: 34px;
}


/*悬浮文本end*/


/*悬浮时 的动画*/

.list_cld:hover .hover_lines>div:nth-child(2) {
	height: 100%;
}

.list_cld:hover .hover_lines>div:nth-child(3) {
	width: 100%;
}

.list_cld:hover .hover_lines>div:nth-child(4) {
	height: 92%;
}

.list_cld:hover .list_cld_hover {
	opacity: 1;
}

.list_cld:hover .hover_txt:nth-child(2) {
	margin-left: 20px;
	margin-top: -7px;
}

.list_cld:hover .hover_txt:nth-child(3) {
	margin-left: -20px;
	margin-top: 7px;
}


/*悬浮时 的动画end*/


